//  div
#map_show { 
  height: 100vh;
  //  100vh 
  min-height: -webkit-fill-available;
}

// ""
.gps_currentPos{
  position: absolute;
  cursor: pointer;
  right: 5vw;
  bottom: 7vh;
  z-index: 999;
  @media screen and (max-width:992px) {
    right: 20px;
    top: 80px;
  }
}

//  marker 
.leaflet-div-icon{
  background: rgba(255, 255, 255, 0);
  border: 0;
}

//  marker 
#lottie_youbike_GPS{
    width: 60px;
    height: 60px;
}

.lendIcon, .returnIcon{
    position:relative;
  img{
    width:36px;
    height:50px;
    position:absolute;
    top:0;
    left:0;
  }
  .bikeNum{
    position:absolute;
    width:36px;
    height:36px;
    top:0;
    left:0;
    display:table;
    span{
      display:table-cell;
      vertical-align:middle;
      text-align: center;
      font-size:16px;
      font-weight:bold;
    }
  }
}
